.NavMenuHome {
  width: 100%;
  max-width: 350px;
  height: 50%;
  position: fixed;
  font-family: Stormlight;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: start;
  z-index: 1000;
  top: 50%;
  left: 12%;
  transform: translate(-50%, -50%);
  animation: startNavHome1 1s;

}

.NavMenuHome span {
  border-style: groove;
  height: 20px;
}

.NavMenuHomeA {
  width: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  position: relative;
}

.NavMenuHomeA p {
  color: #ffffff;
  font-size: 1.3rem;
}

.NavMenuHomeA a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-shadow: #000000 5px 5px 5px;
  position: absolute;
  font-size: 12px;
  left: 15%;
  transition: .3s;
  animation: startNavHome2 2s;
}

.NavMenuHomeA a:hover {
  font-size: 25px;
  text-decoration: underline;
  text-underline-offset: 10px;
}

@keyframes startNavHome1 {
  0% {
    top: 0;
    opacity: 0%;
  }

  100% {
    top: 50%;
    opacity: 100%;
  }
}

@keyframes startNavHome2 {
  0% {
    left: 0%;
    opacity: 0%;
  }

  100% {
    left: 15%;
    opacity: 100%;
  }
}












.secBanner {
  width: 100%;
  position: relative;
  overflow: hidden;
}


.overlayBanner {
  position: absolute;
  width: 50%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


.rectanguleBlack {
  width: 120%;
  max-width: 2000px;
  height: 850px;
  background: linear-gradient(to right, #000000, #0004ff);
  position: absolute;
  transform: rotate(25deg);
  top: 60%;
  left: -15%;
  box-shadow: #0066FF 0px 0px 50px;
}

.imgJuanRbDev {
  position: absolute;
  bottom: 0;
  left: 41%;
}

.banner {
  width: 100%;
  max-width: 1800px;
  height: 100vh;
  max-height: 1000px;
  padding: 10px;
  margin: 0 auto;
  position: relative;
}

.cardIAm {
  position: absolute;
  height: 150px;
  top: 30%;
  left: 80%;
  transform: translate(-50%, -50%);
  text-align: end;
}

.myName {
  font-size: 6rem;
  color: rgb(255, 255, 255);
  font-family: Stormlight;
  letter-spacing: 10px;
  text-shadow: #0066FF 5px 5px 1px;
}

.profetion {
  color: #fff;
  font-family: Revamped;
  letter-spacing: 5px;
  text-shadow: #000000 5px 10px 1px;
}












.aboutMe {
  width: 100%;
  margin: 0 auto;
  height: 100vh;
  max-height: 1000px;
  position: relative;
  overflow: hidden;
  background: #0a0a0a;
  z-index: -1000;
}


.cardAboutImage {
  width: 100%;
  max-width: 400px;
  position: absolute;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  mix-blend-mode: screen;
}


.cardAboutImage02 {
  width: 100%;
  height: 100%;
  object-fit: contain;
  mask-image: url('./assets/img/shape.png');
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
}



.rectanguleBlack2 {
  width: 10%;
  height: 5px;
  background: linear-gradient(#0800ff, #00f7ff);
  position: absolute;
  bottom: 5%;
  left: 0%;
  box-shadow: #0066FF 0px 0px 5px;
  animation: lineasRgb linear 3s infinite;
  border-radius: 50%;
}


@keyframes lineasRgb {
  0% {
    left: -10%;
  }



  100% {
    left: 150%;
  }
}













.skills {
  width: 100%;
  height: 100vh;
  max-height: 1000px;
  background-color: #00ff4c;
}

.projects {
  width: 100%;
  height: 100vh;
  background-color: #0066FF;
}

.contact {
  width: 100%;
  height: 100vh;
  background-color: #00eeff;
}













































@media screen and (max-width: 500px) {

  .secBanner {
    width: 100%;
    position: relative;
    overflow: hidden;
  }

  .overlayBanner {
    position: absolute;
    width: 120%;
    height: 120%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: contain;
  }

  .rectanguleBlack {
    width: 100%;
    max-width: 800px;
    height: 1000px;
    background: linear-gradient(to right, #000000, #0004ff);
    position: absolute;
    transform: rotate(70deg);
    top: 40%;
    left: -25%;
    box-shadow: #ffffff 0px 0px 50px;
  }

  .imgJuanRbDev {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -30%);

  }

  .banner {
    width: 100%;
    height: 100vh;
    padding: 10px;
    position: relative;
  }

  .cardIAm {
    width: 80%;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .myName {
    font-size: 2.5rem;
    color: rgb(255, 255, 255);
    font-family: Stormlight;
    letter-spacing: 10px;
    text-shadow: #0066FF 5px 5px 1px;
  }

  .profetion {
    color: #fff;
    font-family: Revamped;
    letter-spacing: 5px;
    text-shadow: #000000 5px 10px 1px;
  }


}